/* Остальные миксины */
html,
body {
  min-width: 300px;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat-Semibold", "Arial", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 22px;
  line-height: 1.3;
  color: #333333;
  background-color: #e5e5e5;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  overflow-x: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  color: inherit;
  text-decoration: none; }

textarea {
  resize: vertical; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 100%;
  height: 100%;
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }
  @media (max-width: 1024px) {
    .container {
      padding-left: 20px;
      padding-right: 20px; } }
  @media (max-width: 767px) {
    .container {
      padding-left: 16px;
      padding-right: 16px; } }

.btn {
  box-sizing: border-box;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  font-family: inherit;
  font-size: 22px;
  line-height: 26.4px;
  text-align: center;
  color: #ffffff;
  background-color: #02d4bb;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  outline: none;
  transition: 0.5s; }
  .btn:hover, .btn:focus {
    transition: 0.3s; }

.header {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  padding-top: 45px;
  background-color: #ffffff;
  min-height: 840px; }
  .header__logo {
    margin-bottom: 137px;
    display: block;
    width: 100%;
    max-width: 463px;
    height: 78px; }
  .header__title {
    margin: 0;
    margin-bottom: 80px;
    padding: 0;
    width: 100%;
    max-width: 580px;
    font-size: 60px;
    line-height: 76px; }
    .header__title--zero {
      display: inline;
      font-weight: bolder;
      font-size: 150%;
      text-transform: uppercase; }
    .header__title--comission {
      color: #02d4bb;
      display: block; }
  .header__text {
    margin: 0;
    padding: 0;
    font-size: 22px;
    line-height: 1.3;
    color: #333333; }
    .header__text--top {
      width: 100%;
      max-width: 530px;
      margin-bottom: 30px; }
    .header__text--bottom {
      width: 100%;
      max-width: 100%; }
  .header__left {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    padding-top: 15px;
    padding-left: 30px; }
  .header__right {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    padding-top: 45px;
    padding-right: 30px;
    width: 814px;
    height: 840px;
    background-color: #ffffff;
    background-image: url("../img/bg-gradient.png");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover; }

.form__wrapper {
  width: 100%;
  max-width: 500px; }

.form__reg {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px; }

.form__label {
  margin-bottom: 10px;
  display: block;
  color: #ffffff;
  font-family: inherit;
  line-height: 1.1;
  font-size: 22px; }

.form__input {
  width: 100%;
  max-width: 500px;
  padding-top: 20px;
  padding-right: 15px;
  padding-bottom: 20px;
  padding-left: 15px;
  box-sizing: border-box;
  margin-bottom: 25px;
  border-radius: 50px;
  font-family: "Montserrat-Regular", "Arial", sans-serif;
  font-size: 22px;
  line-height: 1.3;
  outline: none;
  border: none;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }
  .form__input--border {
    max-width: calc(100% - 68px);
    border-top-right-radius: 0 0;
    border-bottom-right-radius: 0 0; }

.form__input-wrapper {
  display: flex;
  flex-wrap: wrap; }

.form__input--img {
  width: 68px;
  height: 68px;
  background-color: #02d4bb;
  background-image: url("../img/unions.png");
  background-repeat: no-repeat;
  background-position: 14px center;
  background-size: 50%;
  border-top-right-radius: 50px 50px;
  border-bottom-right-radius: 50px 50px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer; }

.form__wrapper-confirm {
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 15px; }

.form__input--confirm {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.form__label--confirm {
  padding-left: 50px;
  position: relative;
  font-size: 14px;
  line-height: 1.4;
  font-weight: normal;
  font-family: "Montserrat-Regular", "Arial", sans-serif; }

.form__input--checkbox {
  margin-left: -50px;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 25%;
  left: 15%;
  transform: translate(-50%, -50%);
  background-image: url("../img/check-off.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%; }

.form__input--checkbox {
  margin-left: -50px;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 25%;
  left: 15%;
  transform: translate(-50%, -50%);
  background-image: url("../img/check-off.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%; }

.form__input--confirm:checked + .form__input--checkbox {
  background-image: url("../img/check-on.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%; }

.form__btn {
  font-family: inherit;
  font-size: 40px;
  line-height: 60px; }
  .form__btn--signin {
    color: #02d4bb; }
  .form__btn--signup {
    color: rgba(255, 255, 255, 0.5); }

.form__submit {
  padding-top: 26px;
  padding-right: 50px;
  padding-bottom: 26px;
  padding-left: 50px;
  max-width: 294px;
  margin: 0 auto;
  display: block; }
